.App {
  text-align: center;
}

.chatContentBox{
  position: absolute;
  bottom: 50px;
  width: 100%;
}

.chatSubmitBox{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: lightblue;
  padding: 5px 0;
}

.chatSubmitBox input[type="text"]{
  width: calc( 100% - 150px );
  height: 30px;
}

.chatSubmitBox input[type="submit"]{
  width: 100px;
  margin-left: 10px;
  height: 30px;
}